import axios from "axios";
import store from "@/store";
import { Toast } from "vant";
import config from "./../../static/config.json";
const setting = config;

const service = axios.create();
service.defaults.timeout = 1000 * 30; // 请求超时 时长

service.interceptors.request.use(
  config => {
    // do something before request is sent
    config.baseURL = "http://" + store.getters.serverIP + ":" + setting.mainServerPort;
    store.commit("SET_REQUEST", 1);
    return config;
  },
  error => {
    console.log(error); // for debug
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    store.commit("SET_REQUEST", -1);
    return res;
  },
  error => {
    console.log("response=>error:", error);

    if (error.code == "ECONNABORTED" && error.message.indexOf("timeout") > -1) {
      Toast("请求超时~");
    } else if (error.message.indexOf("Network Error") > -1) {
      Toast("当前网络不可用~");
    }
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

const basePath = "/FJRH.Campus/";
const Account = `${basePath}/UserService/`;
const PCService = `${basePath}/PCService/`;
const InteractConfig = `${basePath}/InteractConfigService/`;
const Student = `${basePath}/StuService/`;
const BoardNote = `${basePath}/I_BoardNoteService/`;
const ClassPractise = `${basePath}/I_ClassPractiseService/`;
const Course = `${basePath}/I_CourseService/`;
const Courseware = `${basePath}/I_CoursewareService/`;
const Group = `${basePath}/I_GroupService/`;
const GroupDiscussion = `${basePath}/I_GroupWallService/`;
const ScreenQustion = `${basePath}/I_ScreenSubjectService/`;
const Sign = `${basePath}/I_SignService/`;
const MiniCourse = `${basePath}/I_MicroLessonService/`;
const Note = `${basePath}/I_StudentNoteService/`;
export default {
  get: (httpUrl, data) => {
    return service({
      url: httpUrl,
      method: "get",
      params: data
    });
  },
  post: (httpUrl, data) => {
    return service({
      url: httpUrl,
      method: "post",
      data
    });
  },
  resourse: {
    picture: (id, type) => {
      return `http://${store.getters.serverIP}:${setting.mainServerPort}${basePath}InteractDownload?fileName=${id}&fileType=${type}&platFormType=1`;
    },
    file: id => {
      return `http://${store.getters.serverIP}:${setting.mainServerPort}${basePath}DownloadFileByMID?mid=${id}`;
    }
  },
  account: {
    postLogin: data => {
      return service({
        url: `${Account}Login`,
        method: "post",
        data
      });
    }
  },
  pcService: {
    getData: data => {
      return service({
        url: `${PCService}GetPCData`,
        method: "get",
        params: data
      });
    }
  },
  interactConfig: {
    getData: data => {
      return service({
        url: `${InteractConfig}GetInteractConfigByClsRommID`,
        method: "get",
        params: data
      });
    }
  },
  student: {
    getData: data => {
      return service({
        url: `${Student}GetStudentDetails`,
        method: "get",
        params: data
      });
    }
  },
  boardNote: {
    getList: data => {
      return service({
        url: `${BoardNote}GetDatas`,
        method: "get",
        params: data
      });
    }
  },
  classPractise: {
    getStatistic: data => {
      return service({
        url: `${ClassPractise}GetPLDataAndStats`,
        method: "get",
        params: data
      });
    },
    getData: data => {
      return service({
        url: `${ClassPractise}GetPSAndPA`,
        method: "get",
        params: data
      });
    }
  },
  course: {
    getStudentCourseList: data => {
      return service({
        url: `${Course}GetStudentCourseDatas`,
        method: "get",
        params: data
      });
    },
    getList: data => {
      return service({
        url: `${Course}GetDatas`,
        method: "get",
        params: data
      });
    },
    getStudentScore: data => {
      return service({
        url: `${Course}GetStudentScore`,
        method: "get",
        params: data
      });
    }
  },
  courseware: {
    getList: data => {
      return service({
        url: `${Courseware}GetDatasToStudent`,
        method: "get",
        params: data
      });
    }
  },
  group: {
    getRankList: data => {
      return service({
        url: `${Group}GetGroupMemberRanking`,
        method: "get",
        params: data
      });
    }
  },
  groupDiscussion: {
    getList: data => {
      return service({
        url: `${GroupDiscussion}GetStudentGSDatas`,
        method: "get",
        params: data
      });
    },
    getStatistic: data => {
      return service({
        url: `${GroupDiscussion}GetGSStatsBySubjectID`,
        method: "get",
        params: data
      });
    },
    getData: data => {
      return service({
        url: `${GroupDiscussion}GetGSAndGDDatas`,
        method: "get",
        params: data
      });
    }
  },
  screenQustion: {
    getListByType: data => {
      return service({
        url: `${ScreenQustion}GetGroupByTypeStats`,
        method: "get",
        params: data
      });
    },
    getList: data => {
      return service({
        url: `${ScreenQustion}GetDatas`,
        method: "get",
        params: data
      });
    },
    getData: data => {
      return service({
        url: `${ScreenQustion}GetSSAndSA`,
        method: "get",
        params: data
      });
    }
  },
  sign: {
    getInfo: data => {
      return service({
        url: `${Sign}GetData`,
        method: "get",
        params: data
      });
    }
  },
  miniCourse: {
    getList: data => {
      return service({
        url: `${MiniCourse}GetDatas`,
        method: "get",
        params: data
      });
    }
  },
  note: {
    getList: data => {
      return service({
        url: `${Note}GetDatasByStudent`,
        method: "get",
        params: data
      });
    }
  }
};



